.btn {
  height: 40px;
  border-radius: 40px;
  border: solid 1px $violet;
  padding: 10px 15px 9px;
  text-decoration: none;
  cursor: pointer; }

.btn-violet {
  color: $white;
  font-size: 1.6rem;
  letter-spacing: 0.5px;
  background-image: linear-gradient(to top, $violet, $pink);
  padding: 1.3rem 3rem; }

.btn-white {
  background-color: $transparent;
  letter-spacing: 0.44px;
  color: $violet;
  font-size: 1.4rem; }

.scroll-bottom {
  // position: absolute
  // bottom: 1rem
  // left: 50%
  // transform: translateX(-50%)
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 5rem;
  width: 110px;
  height: 110px;
  border-radius: 50%;
  color: $white;
  background-color: $violet;
  text-decoration: none; }

.video__btn {
  width: 80px;
  height: 80px;
  background-image: linear-gradient(to top, $violet, $pink);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: none;
  color: $white;
  font-size: 3rem;
  margin-bottom: 2.4rem;
  cursor: pointer; }
