%wrapper {
  margin: 0 auto;
  display: none;
  @include mediaSize($phoneVertical) {
    display: block;
    max-width: 300px; }
  @include mediaSize($phone) {
    max-width: 550px; }
  @include mediaSize($tablet) {
    max-width: 750px; }
  @include mediaSize($notebook) {
    max-width: 950px; }
  @include mediaSize($desktop) {
    max-width: 1000px; }
  @include mediaSize($projector) {
    max-width: 1181px; } }

%container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center; }

.wrapper {
  @extend %wrapper; }

.active {
  display: block; }

.hide {
  display: none; }

.textAlign_cen {
  display: flex;
  justify-content: center;
  text-align: center; }
